@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-SemiBold.ttf") format("truetype");
    font-weight: 600;
    font-style: normal;
  }

  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-Medium.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
  }

  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-Regular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
  }

  html,
  body {
    @apply bg-body font-montserrat;
  }

  header {
    @apply bg-main text-white px-7 py-4 h-[10vh];
    @apply fixed w-full top-0 left-0 right-0 flex-between z-40;
  }

  aside {
    @apply fixed h-[90vh] w-[150px] top-[10vh] bg-main text-white;
  }

  /* Form */
  label {
    @apply w-full font-medium text-lg my-5;
  }

  input,
  select,
  textarea {
    @apply w-full bg-transparent outline-none py-2 px-5;
    @apply text-black rounded-10 placeholder:text-black-rgba border border-dark-rgba;
  }

  button {
    @apply font-medium text-lg text-white hover:opacity-90;
  }

  /* ------ Text styles ------ */
  h1 {
    @apply font-semibold text-[20px] leading-6;
  }

  h2 {
    @apply font-semibold text-2xl;
  }

  h3 {
    @apply font-medium text-sm;
  }

  h4 {
    @apply text-base;
  }

  h5 {
    @apply font-semibold text-base w-1/2;
  }

  h6 {
    @apply font-medium text-base text-black-rgba;
  }

  p {
    @apply text-lg leading-5;
  }

  strong {
    @apply font-medium text-3xl text-center;
  }

  span {
    @apply font-semibold text-lg !align-[0px];
  }

  button {
    @apply text-white font-medium text-md leading-6;
  }

  time {
    @apply font-semibold text-base;
  }

  hr {
    @apply text-gray mt-3 mb-5;
  }

  a {
    @apply font-medium text-md;
  }

  /* Table */
  table {
    @apply w-full;
  }

  thead {
    @apply w-full font-semibold text-lg border-light-gray border-b;
  }

  tbody {
    @apply w-full text-lg;
  }

  td {
    @apply py-2 text-md;
  }

  tr {
    @apply py-5 border-b border-light-gray;
  }

  /* List Items */
  li {
    @apply flex-between;
  }

  li p {
    @apply text-base w-1/2 text-start;
  }
}

@layer components {
  /* Grid */
  .container {
    @apply max-w-full ml-36 pb-10 py-8 px-10;
  }

  .flex-center {
    @apply flex items-center;
  }

  .flex-column {
    @apply flex flex-col justify-between;
  }

  .flex-items-center {
    @apply flex items-center justify-center;
  }

  .flex-between {
    @apply flex items-center justify-between;
  }

  /* Header */
  .search-thumb {
    @apply bg-main absolute right-0 top-[50%] p-1;
    @apply rounded-10 translate-y-[-50%] translate-x-[-20%];
  }

  /* Modal */
  .modal-wrapper {
    @apply fixed z-50 w-full h-full flex-items-center left-0 bottom-0 bg-modal;
  }

  .modal-open {
    @apply opacity-100 transition-opacity ease-in-out duration-500;
  }

  .modal-close {
    @apply opacity-0 transition-opacity ease-in-out duration-500;
  }

  /* Buttons */
  .main-btn {
    @apply flex-center gap-x-2 px-4 py-1.5 rounded-5;
  }

  .order-btn {
    @apply py-2.5 px-4 rounded-10;
  }

  .tab-btn {
    @apply main-btn border-2 border-main rounded-10 py-3 text-main;
  }

  /* Checkbox */
  .work-checkbox {
    @apply cursor-pointer absolute w-10 h-1 bg-light-gray block m-0;
    @apply rounded-full top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2;
  }

  .work-checkbox:after {
    @apply content-[""] absolute top-1/2 left-0 -translate-y-1/2 w-5 h-5 bg-light-gray rounded-full duration-300;
  }

  .work-checkbox-active {
    @apply bg-save after:bg-save after:left-[calc(100%-20px)];
  }

  .border-round {
    @apply border border-dark-rgba px-5 py-1 rounded-10 text-md flex-between;
  }

  /* Oders */
  .order-list {
    @apply text-start w-1/2;
  }

  .order-block {
    @apply max-h-[300px] p-5 bg-white rounded-10;
  }

  /* Others */
  .error {
    @apply text-cancel absolute text-sm mt-1;
  }

  /* And */
  .ant-picker-ok > button,
  [type="button"] {
    @apply bg-[#1677ff];
  }

  .ant-picker-ok > button,
  [type="button"] > span {
    @apply font-montserrat font-normal text-sm;
  }

  .ant-picker {
    @apply p-2.5 mt-2 border-dark-rgba hover:border-dark-rgba rounded-10 !shadow-none;
  }
}

.ant-pagination-options-size-changer {
  display: none !important;
}  

